* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: sans-serif;
  min-height: 100vh;
  background: #886AD3; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #886AD3 0%,#38C5C8 70%,#38C5C8 70%,#38C5C8 100%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(315deg, #886AD3 0%,#38C5C8 70%,#38C5C8 70%,#886AD3 100%);
  background: -o-linear-gradient(315deg, #886AD3 0%,#38C5C8 70%,#38C5C8 70%,#886AD3 100%);
  background: linear-gradient(135deg, #886AD3 0%,#38C5C8 70%,#38C5C8 70%,#886AD3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#886AD3', endColorstr='#38C5C8',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  padding: 20px;
}

.react-datepicker-wrapper input {
  padding: 8px 6px;
  cursor: pointer;
  background: none;
  border: 2px solid white;
  color: white;
  border-radius: 3px;
  width: 100%;
}

.title-pop-shadow {
  -webkit-animation:  glow 3s ease-in-out;
          animation:  glow 3s ease-in-out;
}

@-webkit-keyframes glow {
  0% {
    text-shadow:  0 0 10px white;
  }

  15% {
    text-shadow: 2px 2px 10px rgba(255, 255, 255, 1),
    -2px -2px 10px rgba(255, 255, 255, 1);
  }
  30% {
    text-shadow: 2px 2px 4px rgba(255, 255, 255, .7),
    -2px -2px 4px rgba(255, 255, 255, .7);
  }
  50% {
    text-shadow: 20px 20px 50px rgba(255, 255, 255, .5),
    -20px -20px 50px rgba(255, 255, 255, .5);
  }
}

@keyframes glow {
  0% {
    text-shadow:  0 0 10px white;
  }

  15% {
    text-shadow: 2px 2px 10px rgba(255, 255, 255, 1),
    -2px -2px 10px rgba(255, 255, 255, 1);
  }
  30% {
    text-shadow: 2px 2px 4px rgba(255, 255, 255, .7),
    -2px -2px 4px rgba(255, 255, 255, .7);
  }
  50% {
    text-shadow: 20px 20px 50px rgba(255, 255, 255, .5),
    -20px -20px 50px rgba(255, 255, 255, .5);
  }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* REACT TIMELINE */

.suggestion-opaque>div>div {
  background: rgba(95, 35, 35, 0.5)!important;
  -webkit-transition: background 300ms;
  -o-transition: background 300ms;
  transition: background 300ms;
  border: 2px solid white;
}
.suggestion-opaque>div>div:hover {
  background: rgb(255, 255, 255)!important;
}
.vertical-timeline-element-content .vertical-timeline-element-date {
  color: white!important;
}
.vertical-timeline-element:nth-child(odd):not(.vertical-timeline-element--right) .vertical-timeline-element-content .vertical-timeline-element-date {
  left: 140%!important;
}
.vertical-timeline-element:nth-child(even):not(.vertical-timeline-element--left) .vertical-timeline-element-content .vertical-timeline-element-date {
  right: 140%!important;

}


/* GOOEY SWITCH */

.gp-header{ font-size: 40px; color: #886AD3; font-style: italic; text-align: center; margin-top: 25px;}
.gp-footer{position: fixed; color: rgba(255, 68, 239, 0.425); bottom: 10px; left: 50%; font-style: italic; -webkit-transform: translateX(-50%); -ms-transform: translateX(-50%); transform: translateX(-50%);}
.gp-footer .soc_icons{display: inline-block; color: #886AD3; margin: 0px 10px;}

.gooey-svg {width: 120px; height: 50px;}
::-moz-selection { background: transparent;}
::selection {background: transparent;}


/*TODO: Removed because of XMLHttpRequest bug*/
/*.switchContainer{filter: url("./assets/svg/gooey.xhtml#gooey");-webkit-filter: url("#gooey");position: absolute;left:50%;top:50%; cursor: pointer; transform: translate(-50%, -50%) scale(2);}*/
.switchBg{width:60px;height: 7px;}
.switchBtn{width: 30px;height: 30px;border-radius: 50%; display: inline-block; position: absolute; left: 0; top: -12px; -webkit-transform: translate3d(0,0,0); transform: translate3d(0,0,0);}

.switchBg, .switchBtn{background: #fff; -webkit-transition: 300ms all ease; -o-transition: 300ms all ease; transition: 300ms all ease;}
.switchContainer.switchOn .switchBtn, .switchContainer.switchOn .switchBg{background: #886AD3;}

.switchContainer.switchOn .switchBtn{-webkit-animation: switchOn 250ms linear forwards;animation: switchOn 250ms linear forwards; }
.switchContainer.switchOff .switchBtn{-webkit-animation: switchOff 250ms linear forwards;animation: switchOff 250ms linear forwards; }
@-webkit-keyframes switchOn { 
  0%{-webkit-transform: scale3d(1,1,1) translate3d(0px,0,0);transform: scale3d(1,1,1) translate3d(0px,0,0); -webkit-transform-origin: left center; transform-origin: left center;} 
  40%{-webkit-transform: scale3d(1.4,0.7,0) translate3d(20px,0,0);transform: scale3d(1.4,0.7,0) translate3d(20px,0,0); -webkit-transform-origin: right center; transform-origin: right center;}
  70%{-webkit-transform: scale3d(0.9,1.15,1) translate3d(33px,0,0);transform: scale3d(0.9,1.15,1) translate3d(33px,0,0); }
  100%{-webkit-transform: scale3d(1,1,1) translate3d(30px,0,0);transform: scale3d(1,1,1) translate3d(30px,0,0); }
}
@keyframes switchOn { 
  0%{-webkit-transform: scale3d(1,1,1) translate3d(0px,0,0);transform: scale3d(1,1,1) translate3d(0px,0,0); -webkit-transform-origin: left center; transform-origin: left center;} 
  40%{-webkit-transform: scale3d(1.4,0.7,0) translate3d(20px,0,0);transform: scale3d(1.4,0.7,0) translate3d(20px,0,0); -webkit-transform-origin: right center; transform-origin: right center;}
  70%{-webkit-transform: scale3d(0.9,1.15,1) translate3d(33px,0,0);transform: scale3d(0.9,1.15,1) translate3d(33px,0,0); }
  100%{-webkit-transform: scale3d(1,1,1) translate3d(30px,0,0);transform: scale3d(1,1,1) translate3d(30px,0,0); }
}
@-webkit-keyframes switchOff { 
  0%{-webkit-transform: scale3d(1,1,1) translate3d(30px,0,0);transform: scale3d(1,1,1) translate3d(30px,0,0); -webkit-transform-origin: right center; transform-origin: right center;} 
  40%{-webkit-transform: scale3d(1.4,0.7,0) translate3d(10px,0,0);transform: scale3d(1.4,0.7,0) translate3d(10px,0,0); -webkit-transform-origin: right center; transform-origin: right center;}
  70%{-webkit-transform: scale3d(0.9,1.15,1) translate3d(-3px,0,0);transform: scale3d(0.9,1.15,1) translate3d(-3px,0,0); }
  100%{-webkit-transform: scale3d(1,1,1) translate3d(0px,0,0);transform: scale3d(1,1,1) translate3d(0px,0,0); }
}
@keyframes switchOff { 
  0%{-webkit-transform: scale3d(1,1,1) translate3d(30px,0,0);transform: scale3d(1,1,1) translate3d(30px,0,0); -webkit-transform-origin: right center; transform-origin: right center;} 
  40%{-webkit-transform: scale3d(1.4,0.7,0) translate3d(10px,0,0);transform: scale3d(1.4,0.7,0) translate3d(10px,0,0); -webkit-transform-origin: right center; transform-origin: right center;}
  70%{-webkit-transform: scale3d(0.9,1.15,1) translate3d(-3px,0,0);transform: scale3d(0.9,1.15,1) translate3d(-3px,0,0); }
  100%{-webkit-transform: scale3d(1,1,1) translate3d(0px,0,0);transform: scale3d(1,1,1) translate3d(0px,0,0); }
}


/* GOOEY SPINNER */ 
.gooey-container {
  width: 200px;
  height: 200px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: auto;
  -webkit-filter: url('#goo');
          filter: url('#goo');
  -webkit-animation: rotate-move 2s ease-in-out infinite;
          animation: rotate-move 2s ease-in-out infinite;
}

.dot { 
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #000;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.dot-3 {
  background-color: #38C5C8;
  -webkit-animation: dot-3-move 2s ease infinite, index 6s ease infinite;
          animation: dot-3-move 2s ease infinite, index 6s ease infinite;
}

.dot-2 {
  background-color: rgb(106, 122, 211);
  -webkit-animation: dot-2-move 2s ease infinite, index 6s -4s ease infinite;
          animation: dot-2-move 2s ease infinite, index 6s -4s ease infinite;
}

.dot-1 {
  background-color: #886AD3;
  -webkit-animation: dot-1-move 2s ease infinite, index 6s -2s ease infinite;
          animation: dot-1-move 2s ease infinite, index 6s -2s ease infinite;
}

@-webkit-keyframes dot-3-move {
  20% {-webkit-transform: scale(1);transform: scale(1)}
  45% {-webkit-transform: translateY(-18px) scale(.45);transform: translateY(-18px) scale(.45)}
  60% {-webkit-transform: translateY(-90px) scale(.45);transform: translateY(-90px) scale(.45)}
  80% {-webkit-transform: translateY(-90px) scale(.45);transform: translateY(-90px) scale(.45)}
  100% {-webkit-transform: translateY(0px) scale(1);transform: translateY(0px) scale(1)}
}

@keyframes dot-3-move {
  20% {-webkit-transform: scale(1);transform: scale(1)}
  45% {-webkit-transform: translateY(-18px) scale(.45);transform: translateY(-18px) scale(.45)}
  60% {-webkit-transform: translateY(-90px) scale(.45);transform: translateY(-90px) scale(.45)}
  80% {-webkit-transform: translateY(-90px) scale(.45);transform: translateY(-90px) scale(.45)}
  100% {-webkit-transform: translateY(0px) scale(1);transform: translateY(0px) scale(1)}
}

@-webkit-keyframes dot-2-move {
  20% {-webkit-transform: scale(1);transform: scale(1)}
  45% {-webkit-transform: translate(-16px, 12px) scale(.45);transform: translate(-16px, 12px) scale(.45)}
  60% {-webkit-transform: translate(-80px, 60px) scale(.45);transform: translate(-80px, 60px) scale(.45)}
  80% {-webkit-transform: translate(-80px, 60px) scale(.45);transform: translate(-80px, 60px) scale(.45)}
  100% {-webkit-transform: translateY(0px) scale(1);transform: translateY(0px) scale(1)}
}

@keyframes dot-2-move {
  20% {-webkit-transform: scale(1);transform: scale(1)}
  45% {-webkit-transform: translate(-16px, 12px) scale(.45);transform: translate(-16px, 12px) scale(.45)}
  60% {-webkit-transform: translate(-80px, 60px) scale(.45);transform: translate(-80px, 60px) scale(.45)}
  80% {-webkit-transform: translate(-80px, 60px) scale(.45);transform: translate(-80px, 60px) scale(.45)}
  100% {-webkit-transform: translateY(0px) scale(1);transform: translateY(0px) scale(1)}
}

@-webkit-keyframes dot-1-move {
  20% {-webkit-transform: scale(1);transform: scale(1)}
  45% {-webkit-transform: translate(16px, 12px) scale(.45);transform: translate(16px, 12px) scale(.45)}
  60% {-webkit-transform: translate(80px, 60px) scale(.45);transform: translate(80px, 60px) scale(.45)}
  80% {-webkit-transform: translate(80px, 60px) scale(.45);transform: translate(80px, 60px) scale(.45)}
  100% {-webkit-transform: translateY(0px) scale(1);transform: translateY(0px) scale(1)}
}

@keyframes dot-1-move {
  20% {-webkit-transform: scale(1);transform: scale(1)}
  45% {-webkit-transform: translate(16px, 12px) scale(.45);transform: translate(16px, 12px) scale(.45)}
  60% {-webkit-transform: translate(80px, 60px) scale(.45);transform: translate(80px, 60px) scale(.45)}
  80% {-webkit-transform: translate(80px, 60px) scale(.45);transform: translate(80px, 60px) scale(.45)}
  100% {-webkit-transform: translateY(0px) scale(1);transform: translateY(0px) scale(1)}
}

@-webkit-keyframes rotate-move {
  55% {-webkit-transform: translate(-50%, -50%) rotate(0deg);transform: translate(-50%, -50%) rotate(0deg)}
  80% {-webkit-transform: translate(-50%, -50%) rotate(360deg);transform: translate(-50%, -50%) rotate(360deg)}
  100% {-webkit-transform: translate(-50%, -50%) rotate(360deg);transform: translate(-50%, -50%) rotate(360deg)}
}

@keyframes rotate-move {
  55% {-webkit-transform: translate(-50%, -50%) rotate(0deg);transform: translate(-50%, -50%) rotate(0deg)}
  80% {-webkit-transform: translate(-50%, -50%) rotate(360deg);transform: translate(-50%, -50%) rotate(360deg)}
  100% {-webkit-transform: translate(-50%, -50%) rotate(360deg);transform: translate(-50%, -50%) rotate(360deg)}
}

@-webkit-keyframes index {
  0%, 100% {z-index: 3}
  33.3% {z-index: 2}
  66.6% {z-index: 1}
}

@keyframes index {
  0%, 100% {z-index: 3}
  33.3% {z-index: 2}
  66.6% {z-index: 1}
}


